const LIGHT_THEME_NAME = window.LIGHT_THEME_NAME

const NAV_POLY_SCROLL_SHOW = 32; //24;
const NAV_POLY_SCROLL_HIDE = 48; //48
// hide nav scroll position Y!
let HIDE_NAV_SCROLL_POS = 256;


